<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal
    },
    data() {
        return {
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Nomi', value: 'name', sortable: false, align: 'center'},
                {text: 'Kodi', value: 'code', sortable: false, align: 'center'},
                {text: 'Soni', value: 'amount', sortable: false, align: 'center'},
                {text: 'Berilgan resurs', value: 'resource', sortable: false, align: 'center'},
                {text: 'Olingan mahsulot (%)', value: 'amount_get', sortable: false, align: 'center'},
                {text: 'Summasi', value: 'total_price', sortable: false, align: 'center'},
                {text: 'Sana', value: 'date', sortable: false, align: 'center'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: []
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh();
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.fctory_closed_orders': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FACTORY_CLOSED_ORDERS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            factory_sewing_history: state => state.factory.factory_sewing_history
        })
    },
    methods: {
        ...mapActions({
            fetchFactorySewingHistory: 'factory/fetchFactorySewingHistory'
        }),
        async refresh(){
            this.loading = true
            await this.fetchFactorySewingHistory({page: this.page, factory_id: this.$route.params.id, status: 'finished'})
            this.page = this.factory_sewing_history.current_page;
            this.pageCount = Math.ceil(this.factory_sewing_history.total / 10);
            this.items = this.factory_sewing_history.data;
            this.items.forEach((elem) => {
                elem.name = elem.product.name;
                elem.code = elem.product.code;
                elem.amount_get = 0;
                elem.order_incomes.forEach((elem1) => {
                    elem.amount_get = elem.amount_get + elem1.amount
                })
                elem.amount_get_percentage = (elem.amount_get * 100) / elem.amount
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            console.log(this.items)
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        }
    }
};
</script>

