<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import AddModal from '@/components/modals/Factory/Add.vue'
import ProductTookModal from '@/components/modals/Factory/ProductTook.vue'
import CloseProductModal from '@/components/modals/Factory/CloseProductModal'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        AddModal,
        ProductTookModal,
        CloseProductModal,
        DeleteModal
    },
    data() {
        return {
            addDialog: false,
            productTookDialog: false,
            closeProductDialog: false,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Nomi', value: 'name', sortable: false},
                {text: 'Kodi', value: 'code', sortable: false},
                {text: 'Soni', value: 'amount', sortable: false},
                {text: 'Qoldi', value: 'remained', sortable: false},
                {text: 'Berilgan resurs', value: 'resource_given', sortable: false},
                {text: 'Dona narxi', value: 'price', sortable: false},
                {text: 'Berilgan sana', value: 'date', sortable: false},
                {text: 'Qo`shimcha', value: 'additional', sortable: false},
                {text: 'Olingan mahsulot', value: 'product_get', sortable: false},
                {text: 'Kelishuv', value: 'agreement', sortable: false},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
                // {text: 'Sana', value: 'date'},
            ],
            tab: '',
            items: [],
            item: {}
        }
    },
    async mounted(){
        await this.refresh(this.page)
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh();
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.factory_current_process': {
            handler: async function(value) {
                if(value){
                    await this.refresh();
                    this.$store.commit('main/SET_UPDATE_FACTORY_CURRENT_PROCESS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            factory_sewing_history: state => state.factory.factory_sewing_history
        })
    },
    methods: {
        ...mapActions({
            fetchFactorySewingHistory: 'factory/fetchFactorySewingHistory',
            postFinishedOrder: 'factory/postFinishedOrder'
        }),
        async refresh(page){
            this.loading = true;
            await this.fetchFactorySewingHistory({page: this.page, factory_id: this.$route.params.id, status: 'in_process'});
            this.page = this.factory_sewing_history.current_page;
            this.pageCount = Math.ceil(this.factory_sewing_history.total / 10);
            this.items = this.factory_sewing_history.data;
            this.items.forEach((elem) => {
                elem.name = elem.product.name;
                elem.code = elem.product.code;

                elem.amount_get = 0;
                elem.order_incomes.forEach((elem1) => {
                    elem.amount_get = elem.amount_get + elem1.amount
                })
                elem.remained = elem.amount - elem.amount_get
                // elem.amount_get_percentage = (elem.amount_get * 100) / elem.amount

                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY');
            })
            this.loading = false;
        },
        openAddDialog(item){
            this.item = item;
            this.addDialog = true;
        },
        openProductTookDialog(item){
            this.item = item;
            this.productTookDialog = true;
        },
        openCloseProductDialog(item){
            this.item = item;
            this.closeProductDialog = true;
        },
        closeEditModal(){
            this.editModal = false
        }
    }
};
</script>

